.button {
  display: block;
  font-family: inherit;
  font-size: 21px;
  line-height: 26px;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  padding: 16px;
  background-color: #81b3d2;
  border: none;
}
.button:hover,
.button:focus-visible {
  outline: none;
  background-color: #669ec0;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
.button:disabled {
  background-color: #e5e5e5;
}
.button--modal {
  font-size: 18px;
  padding-top: 18px;
  padding-bottom: 14px;
}
@media (min-width: 768px) {
  .button--modal {
    font-size: 21px;
  }
}
.page-header {
  position: relative;
}
.page-header__logo {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  z-index: 1;
}
.page-header__logo--nojs {
  position: static;
}
@media (max-width: 767px) {
  .main-navigation {
    position: relative;
    min-height: 56px;
  }
  .main-navigation__toggle {
    position: absolute;
    display: block;
    color: #81b3d2;
    width: 63px;
    height: 55px;
    padding: 0;
    background-color: #ffffff;
    border: none;
    right: 0;
    top: 0;
    z-index: 3;
    cursor: pointer;
  }
  .main-navigation__list {
    position: absolute;
    top: 0;
    z-index: 2;
  }
  .main-navigation--closed .main-navigation__list {
    display: none;
  }
  .main-navigation--opened .main-navigation__list {
    display: block;
  }
  .main-navigation--opened .main-navigation__toggle {
    color: #ffffff;
    background-color: #81b3d2;
  }
  .main-navigation--nojs .main-navigation__toggle {
    display: none;
  }
  .main-navigation--nojs .main-navigation__list {
    display: block;
    position: static;
  }
  .main-navigation__icon {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  .main-navigation__icon--closed {
    display: none;
  }
  .main-navigation__icon--opened {
    display: none;
  }
  .main-navigation--closed .main-navigation__icon--opened {
    display: block;
  }
  .main-navigation--opened .main-navigation__icon--closed {
    display: block;
  }
  .main-navigation__toggle:focus-visible {
    outline: none;
  }
  .main-navigation__toggle:hover .main-navigation__icon,
  .main-navigation__toggle:focus-visible .main-navigation__icon {
    opacity: 60%;
  }
  .main-navigation__toggle:active .main-navigation__icon {
    opacity: 30%;
  }
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
  .main-navigation__list {
    position: static;
  }
}
.logo {
  display: block;
  width: 185px;
}
.logo:hover[href],
.logo:focus[href] {
  opacity: 60%;
  outline: none;
}
.logo:active[href] {
  opacity: 30%;
}
.site-list {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.site-list__item .site-list__link {
  box-shadow: 0 1px 0 0 rgba(255, 255, 255, 0.2) inset, 0 -1px 0 0 rgba(1, 1, 1, 0.2) inset;
}
.site-list__item:first-child .site-list__link {
  box-shadow: 0 -1px 0 0 rgba(1, 1, 1, 0.2) inset;
}
.site-list__item:last-child .site-list__link {
  box-shadow: 0 1px 0 0 rgba(255, 255, 255, 0.2) inset, 0 -4px 5px 0 rgba(0, 0, 0, 0.2) inset;
}
.site-list__link {
  display: block;
  font-family: inherit;
  font-size: 18px;
  line-height: 26px;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
  font-weight: 700;
  padding: 17px 20px 13px 20px;
  background-color: #81b3d2;
}
.site-list__link:hover[href] {
  background-color: #669ec0;
}
.site-list__link:active[href] {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
.site-list__link:focus-visible {
  outline: none;
  background-color: #669ec0;
}
@media (min-width: 768px) {
  .site-list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }
  .site-list__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .site-list__item:nth-child(3) {
    grid-column: 4 / 5;
  }
  .site-list__item:nth-child(7) {
    grid-column: 4 / 5;
  }
  .site-list__item .site-list__link {
    box-shadow: none;
  }
  .site-list__item:first-child .site-list__link {
    box-shadow: none;
  }
  .site-list__item:last-child .site-list__link {
    box-shadow: none;
  }
  .site-list__link {
    display: block;
    font-size: inherit;
    line-height: inherit;
    white-space: break-spaces;
    color: #000000;
    padding: 20px 20px 15px 20px;
    background-color: #ffffff;
  }
  .site-list__link:hover[href] {
    color: #81b3d2;
    background-color: #ffffff;
  }
  .site-list__link:active[href] {
    color: rgba(0, 0, 0, 0.3);
    background-color: #ffffff;
  }
  .site-list__link--current {
    color: #766357;
  }
  .site-list__link:focus-visible {
    outline: none;
    background-color: #e5e5e5;
  }
}
